import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';

import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, api_p2b_url } from '../../config';
import { not } from '@angular/compiler/src/output/output_ast';
import { CurrencyPipe } from '@angular/common';

import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';

/**
 * Generated class for the PrpoDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-prpo-detail',
  providers: [SoapService],
  templateUrl: 'prpo-detail.html',
})
export class PrpoDetailPage {
  userdataTPK:any;
  prpoData: any;
  isLoadingLine: Boolean = true;
  isLoadingAppr: Boolean = true;
  isLoadingHdr: Boolean = true;
  lineData: Array<any> = [];
  apprData: Array<any> = [];
  hdrData: Array<any> = [];

  scTypeLines: any = '';
  scTypeAppr: any = '';
  scTypeHdr: any = '';
  forwardTo: any = '';
  note: any = '';

  searchDataList: Array<any> = [];
  showResult: Boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,
    public currencyPipe: CurrencyPipe,
    public inAppBrowser: InAppBrowser
  ) {
    this.prpoData = navParams.get('prpoData');

    this.storage.get('userdataTPK').then(val => {
      console.log(val);
      this.userdataTPK = val;      
    });

    if (this.prpoData['DOCUMENT_TYPE'] == 'REQUISITION') {
      this.scTypeLines = 'pr_lines_tpk';
      this.scTypeAppr = 'pr_appr_tpk';
      this.scTypeHdr = '';
      this.isLoadingHdr = false;
    } else if (this.prpoData['DOCUMENT_TYPE'] == 'PO') {
      this.scTypeLines = 'po_lines_tpk';
      this.scTypeAppr = 'po_appr_tpk';
      this.scTypeHdr = 'po_hdr_tpk';
      this.getHeader();
    } else {
      console.log('error here');
    }

    this.getLine();
    this.getAppr();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PrpoDetailPage');
  }

  getLine() {
    this.isLoadingLine = true;
    this.soapService
      .post(api_p2b_url, 'eoffice_bypass_wso', {
        fStream: JSON.stringify(
          {
            sc_type: this.scTypeLines,
            sc_code: "",
            data: {
              document_id: this.prpoData['DOCUMENT_ID']
            }
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          if (this.isEmptyObject(responData['data'][0]) == false) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.lineData.push(responData['data'][i]);
            }
          }
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data line, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
        this.isLoadingLine = false;
      })
      .catch(error => {
        console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data line, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.isLoadingLine = false;
      });
  }

  getAppr() {
    this.isLoadingAppr = true;
    this.soapService
      .post(api_p2b_url, 'eoffice_bypass_wso', {
        fStream: JSON.stringify(
          {
            sc_type: this.scTypeAppr,
            sc_code: "",
            data: {
              document_id: this.prpoData['DOCUMENT_ID']
            }
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          if (this.isEmptyObject(responData['data'][0]) == false) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.apprData.push(responData['data'][i]);
            }
          }
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data Appr, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        this.isLoadingAppr = false;

      })
      .catch(error => {
        console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data Appr, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.isLoadingAppr = false;
      });
  }

  getHeader() {
    this.isLoadingHdr = true;
    this.soapService
      .post(api_p2b_url, 'eoffice_bypass_wso', {
        fStream: JSON.stringify(
          {
            sc_type: this.scTypeHdr,
            sc_code: "",
            data: {
              document_id: this.prpoData['DOCUMENT_ID']
            }
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          if (this.isEmptyObject(responData['data'][0]) == false) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.hdrData.push(responData['data'][i]);
            }
          }
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data Header, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        this.isLoadingHdr = false;

      })
      .catch(error => {
        console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data Header, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.isLoadingHdr = false;
      });
  }

  doAction(action) {
    var msg = '';
    if (action == 'APPROVE') {
      msg = 'Apakah anda yakin ingin Approve ?';
    } else if (action == 'APPROVE_AND_FORWARD') {
      msg = 'Apakah anda yakin ingin Approve and Forward ?';
    } else if (action == 'FORWARD') {
      msg = 'Apakah anda yakin ingin Forward ?';
    } else if (action == 'REJECT') {
      msg = 'Apakah anda yakin ingin Reject ?';
    } else {
      msg = 'Apakah anda yakin ?';
    }

    let alert = this.alertCtrl.create({
      subTitle: msg,
      cssClass: 'alert',
      buttons: [
        {
          text: 'TIDAK',
          role: 'cancel',
          handler: () => { }
        },
        {
          text: 'YA',
          handler: () => {
            let loading = this.loadingCtrl.create({
              spinner: 'dots',
              content: "Mohon Tunggu...",
              cssClass: 'transparent',
              dismissOnPageChange: true
            });
            loading.present();
            this.soapService
              .post(api_p2b_url, 'eoffice_bypass_wso', {
                fStream: JSON.stringify(
                  {
                    sc_type: "act_worklist_prpo_tpk",
                    sc_code: "",
                    data: {
                      notification_id: this.prpoData['NOTIFICATION_ID'],
                      performer: this.userdataTPK['data']['NIPP'],
                      action: action,
                      forward_to: this.forwardTo,
                      note: this.note
                    }
                  }
                )
              }).then(result => {
                var responData = JSON.parse(String(result));
                console.log(responData);
                if (responData['rcmsg'] == "SUCCESS") {
                  let toast = this.toastCtrl.create({
                    message: action + ' Berhasil',
                    duration: 3000,
                    position: 'bottom'
                  });
                  toast.present().then(() => {
                    //this.pushNotif();
                    this.navCtrl.pop();
                  });
                } else {
                  let toast = this.toastCtrl.create({
                    message: 'Gagal mengirim action, silahkan coba kembali.',
                    duration: 3000,
                    position: 'bottom'
                  });
                  toast.present();
                }

                loading.dismiss();

              })
              .catch(error => {
                console.log(error);
                let toast = this.toastCtrl.create({
                  message: 'Gagal mengirim action, periksa koneksi internet anda.',
                  duration: 3000,
                  position: 'bottom'
                });
                toast.present();
                loading.dismiss();
              });
          }
        }
      ]
    });
    

    if (action == 'APPROVE_AND_FORWARD' || action == 'FORWARD') {
      if (this.forwardTo != '' || this.forwardTo != '') {
        alert.present();
      } else {
        let toast = this.toastCtrl.create({
          message: 'Field Forward To harus diisi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      }
    } else {
      alert.present();
    }
  }

  isEmptyObject(obj) {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return true;
  }

  getForward() {
    if (this.forwardTo.length > 2) {
      console.log(this.forwardTo);
      this.soapService
        .post(api_base_url, 'eoffice_findemployee', {
          fStream: JSON.stringify(
            {
              usernameEDI: api_user,
              passwordEDI: api_pass,
              search: this.forwardTo.toUpperCase(),
              atas: 1,
              bawah: 5
            }
          )
        }).then(result => {
          var responData = JSON.parse(String(result));
          console.log(responData);
          if (responData['rcmsg'] == "SUCCESS") {
            this.searchDataList = responData['data'];
            this.showResult = true;
          } else {
            let toast = this.toastCtrl.create({
              message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
              duration: 3000,
              position: 'bottom'
            });
            toast.present();
            this.showResult = false;
            this.forwardTo = '';          
          }
        })
        .catch(error => {
          console.log(error);
          let toast = this.toastCtrl.create({
            message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
          this.showResult = false;
          this.forwardTo = '';
        });
    } else {
      this.showResult = false;
    }
  }

  setForward(data) {
    this.forwardTo = data['NIPP'];
    this.searchDataList = [];
    this.showResult = false;
  }

  OpenSelect() {
    console.log('trigered');
    let modal = this.modalCtrl.create('SearchForwardPrpoPage', { titleText: "Please Select" });
    modal.onDidDismiss((data) => {
      console.log(data);   
      if (data != null)   {
        this.forwardTo = data['NIPP'];
      }
    });
    modal.present();
  }

  convertCurrency(money, money2) {
    if (money != '') {
      money = parseInt(money);
      let hasil = this.currencyPipe.transform(money, 'IDR');
      let hasilSpit = hasil.split('.');
      console.log(hasilSpit[0]);    
      return hasilSpit[0].substr(3);
    } else {
      return '0';
    }
  }

  decode(data) {
    return atob(data);
  }

  goToURL(data) {
    const options: InAppBrowserOptions = {
      zoom: 'no'
    }

    const browser = this.inAppBrowser.create(data, '_system', options);
  }


}



